<template>
  <div>
			<div class="Detailsimg-div1"></div>
			<Switchthebar2 :listArr="Details2"></Switchthebar2>
			<div class="Detailsimg-div2"></div>
			<div v-for="obj in listimg"><img :src="obj.imgUrl"/></div>
			<div style="padding: 0.43rem;display: block;">			
			</div>
  </div>
</template>
<script lang="babel">
import Switchthebar2 from "./Switchthebar2";
export default {
	props: {//
		listimg:Array
		},
	data(){//
		return{	
			Details2:[]
		}	
	},
	methods:{//js
		
	},
	components:{//
		Switchthebar2
	},
	updated() {
	
	},
	mounted(){//ajax
				var _this = this;

			_this.$http({
				method: 'get', //ajax 
				url: "./static/json/Details2.json", //()
				params: {} //data 
			}).then(function(data) {
				//data;success //sliderArr,sliderArr
				_this.Details2 = data.data; //_this.xxxArr=data.body;
			}, function(error) {
				//
				alert("");
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
	.Detailsimg-div1{
		 margin-top: 0em;
    padding: 0.5em 0.5em;
    background: #FFF;
    color: #6C6B6A;
    font-size: 0.9em;
    border-left: #ff0000 solid 4px;
	}
	.Detailsimg-div2{
		 margin-top: 0.3em;
    padding: 0.5em 0.5em;
    background: #FFF;
    color: #6C6B6A;
    font-size: 0.9em;
    border-left: #ff0000 solid 4px;
    margin-bottom: 0.1em;
	}
</style>


